* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  background-color: #eae2b7;
  font-family: main-font;
}

.time {
  color: white;
  position: fixed;
  right: -3rem;
  top: 2rem;
  font-size: 5.5rem;
  font-weight: 500;
  padding: .5rem 2rem;
  padding-left: 2.5rem;
  border-radius: 30px;
  z-index: 10;

  background-position: center 55%;
  background-repeat: no-repeat;
  background-size: 100%;
  // padding: 1rem;
  min-width: 15.6rem;
  font-family: "dafor-font";

}

.theme {
  position: fixed;
  right: 0;
  bottom: 0;

  span:nth-child(1) {
    background-color: #d62828;
    width: 22rem;
    height: 22rem;
    position: absolute;
    right: 0;
    bottom: 0;
    border-radius: 100%;
    transform: translate(50%, 50%);
    z-index: 3;
  }

  span:nth-child(2) {
    background-color: #f77f00;
    width: 35rem;
    height: 35rem;
    position: absolute;
    right: 10px;
    bottom: 10px;
    border-radius: 120px;
    transform: translate(50%, 50%) rotate(-45deg);
    z-index: 2;
  }

  span:nth-child(3) {
    background-color: #fcbf49;
    width: 50rem;
    height: 50rem;
    position: absolute;
    right: 0;
    bottom: 0;
    border-radius: 120px;
    transform: translate(50%, 50%) rotate(-45deg);
    z-index: 1;
  }
}


.search-container {
  width: 22rem;
  margin-left: 10vw;
  display: flex;
  position: relative;
  border-radius: 100px;
  margin-top: 10rem;

  input {
    width: 100%;
    background-color: #003049;
    border: none;
    outline: none;
    height: 4rem;
    border-radius: 6rem;
    color: white;
    font-size: 1.5rem;
    padding: 1rem;
    padding-right: 4rem;
    font-family: main-font;
    // text-align: end;
    direction: rtl;
  }

  .icon {
    position: absolute;
    background: linear-gradient(-130deg, #005786, #003049);
    width: 2.8rem;
    height: 2.8rem;
    padding: .5rem;
    border-radius: 100%;
    right: .5rem;
    top: .5rem;
    transition: .2s;

    &:hover {
      transform: scale(1.1);
    }

    &:active {
      transform: scale(.8);
    }
  }
}

.card-container {
  margin-top: 50px;
  margin-left: 10vw;
  position: relative;
  width: 40rem;
  height: 25rem;
  background-color: #003049;
  z-index: 10;
  border-radius: 2.5rem;
  padding: 1.3rem;

  .card {
    border: 5px solid white;
    height: 100%;
    border-radius: 3rem;
    color: white;
    padding: 2.5rem 2rem;
    display: flex;
    justify-content: space-between;

    .col-1 {
      display: flex;
      flex-flow: column;
      font-family: main-font;
      align-items: flex-start;

      * {
        margin-bottom: .2rem;
      }

      .temp {
        font-size: 4rem;
        margin-top: -.7rem;
        margin-bottom: -.6rem;
        position: relative;

        &::after {
          content: 'o';
          position: absolute;
          right: -1rem;
          top: 0;
          width: 1rem;
          font-size: 2rem;
          font-weight: 600;
          font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        }
      }

      span {
        height: 5px;
        width: 50%;
        border-radius: 5rem;
        background-color: white;
      }

      h1 {
        font-size: 3rem;
        font-weight: 500;
      }

      h2 {
        font-weight: 400;
        font-size: 2.3rem;
      }

      h3 {
        font-size: 1.7rem;
        font-weight: 500;
      }
    }

    .col-2 {
      img {
        height: 70%;
      }
    }

    .error {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-weight: 400;
      font-size: 5rem;
    }
  }

}

@media (min-width:1200px) {
  .card-container {
    width: 45rem;
    height: 30rem;
  }
}

@media (max-width:750px) {
  :root {
    font-size: 10px;
  }

  .time {
    top: 0px;
    left: 50%;
    display: flex;
    flex-flow: column-reverse;
    align-items: center;
    transform: translate(-50%, -75%);
    min-width: 50px;
    width: 30%;
    height: 100px;
    background: linear-gradient(35deg, #f77f00 10%, #d62828 50%);
    background-size: contain;
    border-radius: 200px;
    padding-top: 290px;
    font-size: 4.5rem;
  }

  .search-container {
    margin: auto;
    margin-top: 12rem;
    width: 300px;

  }

  .card-container {
    margin: auto;
    width: 300px;
    margin-top: 3rem;
    height: 450px;

    .card {
      flex-flow: column;
      text-align: center;

      .col-1 {

        // align-items: center;
        h3 {
          font-weight: 100;
        }
      }

      .col-2 {
        img {
          height: 100%;
          width: 100%;
        }
      }
    }
  }

  .theme {
    span:nth-child(1) {
      border-radius: 0;
      width: 1000px;
      transform: rotate(-45deg) translate(50%, 180%);
    }

    span:nth-child(2) {
      border-radius: 0;
      width: 1500px;
      bottom: 120px;
      right: 120px;
    }

    span:nth-child(3) {
      border-radius: 0;
      width: 1500px;
      bottom: 200px;
      right: 200px;
    }

  }
}