@font-face {
  font-family: dafor-font;
  src: url(bcb2abf0980ed4ef.TTF);
}

@font-face {
  font-family: main-font;
  src: url(sultan-font.ttf);
  font-weight: 500;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #eae2b7;
  font-family: main-font;
}

.time {
  color: white;
  position: fixed;
  right: -3rem;
  top: 2rem;
  font-size: 5.5rem;
  font-weight: 500;
  padding: .5rem 2rem;
  padding-left: 2.5rem;
  border-radius: 30px;
  z-index: 10;
  background-position: center 55%;
  background-repeat: no-repeat;
  background-size: 100%;
  min-width: 15.6rem;
  font-family: "dafor-font";
}

.theme {
  position: fixed;
  right: 0;
  bottom: 0;
}

.theme span:nth-child(1) {
  background-color: #d62828;
  width: 22rem;
  height: 22rem;
  position: absolute;
  right: 0;
  bottom: 0;
  border-radius: 100%;
  -webkit-transform: translate(50%, 50%);
          transform: translate(50%, 50%);
  z-index: 3;
}

.theme span:nth-child(2) {
  background-color: #f77f00;
  width: 35rem;
  height: 35rem;
  position: absolute;
  right: 10px;
  bottom: 10px;
  border-radius: 120px;
  -webkit-transform: translate(50%, 50%) rotate(-45deg);
          transform: translate(50%, 50%) rotate(-45deg);
  z-index: 2;
}

.theme span:nth-child(3) {
  background-color: #fcbf49;
  width: 50rem;
  height: 50rem;
  position: absolute;
  right: 0;
  bottom: 0;
  border-radius: 120px;
  -webkit-transform: translate(50%, 50%) rotate(-45deg);
          transform: translate(50%, 50%) rotate(-45deg);
  z-index: 1;
}

.search-container {
  width: 22rem;
  margin-left: 10vw;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
  border-radius: 100px;
  margin-top: 10rem;
}

.search-container input {
  width: 100%;
  background-color: #003049;
  border: none;
  outline: none;
  height: 4rem;
  border-radius: 6rem;
  color: white;
  font-size: 1.5rem;
  padding: 1rem;
  padding-right: 4rem;
  font-family: main-font;
  direction: rtl;
}

.search-container .icon {
  position: absolute;
  background: linear-gradient(-130deg, #005786, #003049);
  width: 2.8rem;
  height: 2.8rem;
  padding: .5rem;
  border-radius: 100%;
  right: .5rem;
  top: .5rem;
  -webkit-transition: .2s;
  transition: .2s;
}

.search-container .icon:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.search-container .icon:active {
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
}

.card-container {
  margin-top: 50px;
  margin-left: 10vw;
  position: relative;
  width: 40rem;
  height: 25rem;
  background-color: #003049;
  z-index: 10;
  border-radius: 2.5rem;
  padding: 1.3rem;
}

.card-container .card {
  border: 5px solid white;
  height: 100%;
  border-radius: 3rem;
  color: white;
  padding: 2.5rem 2rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.card-container .card .col-1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-flow: column;
          flex-flow: column;
  font-family: main-font;
  -webkit-box-align: start;
      -ms-flex-align: start;
          align-items: flex-start;
}

.card-container .card .col-1 * {
  margin-bottom: .2rem;
}

.card-container .card .col-1 .temp {
  font-size: 4rem;
  margin-top: -.7rem;
  margin-bottom: -.6rem;
  position: relative;
}

.card-container .card .col-1 .temp::after {
  content: 'o';
  position: absolute;
  right: -1rem;
  top: 0;
  width: 1rem;
  font-size: 2rem;
  font-weight: 600;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.card-container .card .col-1 span {
  height: 5px;
  width: 50%;
  border-radius: 5rem;
  background-color: white;
}

.card-container .card .col-1 h1 {
  font-size: 3rem;
  font-weight: 500;
}

.card-container .card .col-1 h2 {
  font-weight: 400;
  font-size: 2.3rem;
}

.card-container .card .col-1 h3 {
  font-size: 1.7rem;
  font-weight: 500;
}

.card-container .card .col-2 img {
  height: 70%;
}

.card-container .card .error {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-weight: 400;
  font-size: 5rem;
}

@media (min-width: 1200px) {
  .card-container {
    width: 45rem;
    height: 30rem;
  }
}

@media (max-width: 750px) {
  :root {
    font-size: 10px;
  }
  .time {
    top: 0px;
    left: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: reverse;
        -ms-flex-flow: column-reverse;
            flex-flow: column-reverse;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
    -webkit-transform: translate(-50%, -75%);
            transform: translate(-50%, -75%);
    min-width: 50px;
    width: 30%;
    height: 100px;
    background: linear-gradient(35deg, #f77f00 10%, #d62828 50%);
    background-size: contain;
    border-radius: 200px;
    padding-top: 290px;
    font-size: 4.5rem;
  }
  .search-container {
    margin: auto;
    margin-top: 12rem;
    width: 300px;
  }
  .card-container {
    margin: auto;
    width: 300px;
    margin-top: 3rem;
    height: 450px;
  }
  .card-container .card {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-flow: column;
            flex-flow: column;
    text-align: center;
  }
  .card-container .card .col-1 h3 {
    font-weight: 100;
  }
  .card-container .card .col-2 img {
    height: 100%;
    width: 100%;
  }
  .theme span:nth-child(1) {
    border-radius: 0;
    width: 1000px;
    -webkit-transform: rotate(-45deg) translate(50%, 180%);
            transform: rotate(-45deg) translate(50%, 180%);
  }
  .theme span:nth-child(2) {
    border-radius: 0;
    width: 1500px;
    bottom: 120px;
    right: 120px;
  }
  .theme span:nth-child(3) {
    border-radius: 0;
    width: 1500px;
    bottom: 200px;
    right: 200px;
  }
}
